<script>
import HeaderComponent from "@/components/Header1/header1.vue";
export default {
  name: "printRollComponent",
  components: { HeaderComponent },
  data() {
    return {
      data: {},
      detail: {},
      tableData: [],
      tableList: [],
    };
  },
  computed: {},
  methods: {
    async qryQualifyDetail(schedule_no, available_begin_date, class_date) {
      const res = await this.$store.dispatch("qryQualifyDetail", {
        schedule_no,
        available_begin_date,
        class_date,
      });
      const data = res.data.data;
      this.detail = data;
      if (data.course_Supplier || data.course_Supplier.length) {
        this.tableData = JSON.parse(JSON.stringify(data.course_Supplier));
        this.tableData.forEach((e, index) => {
          e.index = index + 1;
          e.dDate = `${e.select_Date}/ ${e.short_Name}`;
          e.info = `${e.s_Name || ""}/${e.s_id}`;
          e.nameId = `${e.id}/${e.sex || ""}/${e.p_Name || ""}`;
          e.approveStatus = this.getApproveStatus(e.approve_Status);
        });
        // this.tableList = this.tableData.slice(0, 10);
        this.tableList = this.tableData.filter((e) => e.approve_Status === "1");
      }
    },
  },
  mounted() {
    this.data = JSON.parse(this.$route.query.data);
    this.qryQualifyDetail(
      this.data.schedule_No,
      this.data.available_Begin_Date,
      this.data.class_Date
    );
    console.log("this.data: ", this.data);
    this.$root.overlay = false;
    this.$store.commit("HEADER_HANDLER", false);
    this.$store.commit("BREADCRUMB_HANDLER", false);
    this.$root.$children[0].sidebarSwitch = false;
  },
};
</script>

<template src="./printRoll.html"></template>
